@charset "utf-8";
#header { width: 100%; padding: 0 16px; } 
#header .my_info { width: 100%; font-size: 14px; text-align: center; } 
#header .my_info .img_im { width: 100px; height: 100px; border-radius: 16px; overflow: hidden; box-shadow: 0 0 4px 0 rgba(0, 0, 0, .2); margin: 0 auto; } 
#header .my_info .img_im img { width: 100%; height: 100%; object-fit: cover; } 
#header .my_info .tx_id { font-size: 18px; font-weight: bold; text-align: center; margin-top: 20px; } 
#header .my_info .tx_summary { font-size: 16px; margin-top: 10px; margin-bottom: 20px; } 
#header .my_info .tx_link { text-align: left; font-size: 12px; } 
#header .my_info .tx_callnumber { text-align: left; font-size: 12px; } 
#header .my_info .tx_mail { text-align: left; font-size: 12px; } 
#header .my_gnb { margin-top: 20px; } 
#header .my_gnb ul { } 
#header .my_gnb ul li { } 
#header .my_gnb ul li+li { margin-top: 8px; } 
#header .my_gnb ul li a { display: block; line-height: 48px; padding: 0 10px; border-radius: 8px; background-color: rgba(255, 255, 255, .25); color: #ccc; transition: all .3s; } 
#header .my_gnb ul li a:hover { background-color: rgba(255, 255, 255, .8); color: #333; } 
#header .my_theme { margin-top: 20px; } 
#header .my_theme .tx_tit { font-size: 16px; margin-bottom: 10px; } 
#header .my_theme ul { display: flex; flex-wrap: wrap; padding: 8px 10px; border-radius: 8px; background-color: rgba(255, 255, 255, .25); } 
#header .my_theme ul li { width: 32px; height: 32px; margin-right: 8px; } 
#header .my_theme ul li button { width: 32px; height: 32px; border:none; border-radius: 50%; box-shadow: 0 0 4px 0 rgba(0, 0, 0, .2); cursor: pointer; } 
#header .my_theme ul li button[data-color=dark] { background-color: #333; } 
#header .my_theme ul li button[data-color=white] { background-color: #fff; } 
#header .my_theme ul li button[data-color=blue] { background-color: #0073be; } 
#header .my_copyright { font-size: 10px; margin-top: 20px; opacity: .6; text-align: center; } 
.white #header .my_gnb ul li a { background-color: rgba(0, 0, 0, .05); color: #888; } 
.white #header .my_gnb ul li a:hover { background-color: rgba(0, 0, 0, .1); color: #333; } 

.white #header .my_theme ul { background-color: rgba(0, 0, 0, .05); } 