@charset "utf-8";
:root{
    --bs-blue: #0d6efd;
    --bs-indigo: #6610f2;
    --bs-purple: #6f42c1;
    --bs-pink: #d63384;
    --bs-red: #dc3545;
    --bs-orange: #fd7e14;
    --bs-yellow: #ffc107;
    --bs-green: #198754;
    --bs-teal: #20c997;
    --bs-cyan: #0dcaf0;
    --bs-white: #fff;
    --bs-gray: #6c757d;
    --bs-gray-dark: #343a40;
    --bs-primary: #0d6efd;
    --bs-secondary: #6c757d;
    --bs-success: #198754;
    --bs-info: #0dcaf0;
    --bs-warning: #ffc107;
    --bs-danger: #dc3545;
    --bs-light: #f8f9fa;
    --bs-dark: #212529;
    --font-family-common: 13px/1.4 'Noto Sans KR',-apple-system,system-ui,BlinkMacSystemFont,"맑은 고딕","Malgun Gothic","Apple SD Gothic Neo","돋움",dotum,sans-serif;
    --font-family-nanum : 'Nanum Gothic', sans-serif;;
    --font-family-noto : 'Noto Sans KR', sans-serif;
    --font-family-roboto : 'Roboto', sans-serif;
    --font-lg:18px;
    --font-md:16px;
    --font-sm:13px;
    --font-xs:11px;
    --color-enuri-red1 : #d7003c;  /* 에누리 블루 */
    --color-enuri-blue2 : #0073be;  /* 에누리 블루 */
    --color-enuri-blue1 : #30a7f7; /* 에누리 스카이 */
    /* --url-comm-sprite : url('./images/common/common_sprite.png');  */
    /* 공통 Sprite */
    
}
*{
    margin:0;
    padding:0;
}
*, :before, :after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
html,body{
    margin:0;
    padding:0;
    font:var(--font-family-common);
    vertical-align:middle;
}
h1, h2, h3, h4, h5, h6 {
    margin:0;
    padding:0;
}
dl, dt, dd, ol, ul, li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
blockquote,q { quotes:none}
blockquote:before,blockquote:after,q:before,q:after { content:'';content:none}
hr{
    display:none
}
img {
    border:none;
    outline:none;
    max-width: 100%;
}
.clearfix:after {
    content:"";
    display:block;
    clear:both;
    *zoom:1;
}
button {cursor:pointer; }
/* a Css */
a{cursor:pointer; color:var(--theme-anchor); text-decoration:none }
/*
a:link,a:visited,a:active{text-decoration:none}
a:hover{text-decoration:underline} 
a:active{background-color:transparent}
*/

/* form */
legend,caption {
    display:block;
    overflow:hidden;
    position:absolute;
    text-indent:-9999px;
    top:-9999px;
    font-size:0;
    width:1px;
    height:1px;
    left:-9999px;
    line-height:0
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {color:#aaa;}
input:-moz-placeholder,
textarea:-moz-placeholder {color:#aaa;}
input::-moz-placeholder,
textarea::-moz-placeholder {color:#aaa;}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {color:#aaa;}
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {color:transparent;}
input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {color:transparent;}
input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {color:transparent;}
input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {color:transparent;}

