/* ----------------------------------------------------------------
                     태이블 기본
-----------------------------------------------------------------*/

.table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
}
.table-default {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border-top:1px solid #e1e1e1;
}
.table-default thead tr th{
    padding:8px;
    border:1px solid #e0e0e0;
    font-size:16px;
    font-weight:bold;
    background:#f5f5f5;
    color:#666;
    text-align: center;
}
.table-default tbody tr th,
.table-default tbody tr td {
    padding:24px 22px;
    border-bottom:1px solid #e1e1e1;
    vertical-align: middle;
}
.table-default tbody tr th {
    font-size:16px;
    font-weight:bold;
    background:#f5f5f5;
    color:#666;
    text-align: left;
}
.table-default tbody tr td {
    font-size:16px;
    color:#333;
}
/* ------------------------------ */
.table-default tr th .required-value {
    font-size: 14px;
    color:#fa394b;
    margin-left:4px;
}
